<template>
  <!--div-->
  <div id="study">
    <p>{{ msg }}</p>
    <button v-on:click="reverseMessage"></button>
    <p v-once>{{person.username}}</p>
    <p>:{{person.username}}</p>
    <input v-model="person.username" v-bind:title="tip" v-bind:placeholder="tip" v-on:keyup.enter="inputAlert">
    <p v-bind:title="msg">
      
    </p>
    <p v-if="flag">true</p>
    <p v-else>false</p>

    <ol>
      <li v-for="(value,key) in types" :key="value.id">
        {{key}}:{{ value.text }}
      </li>
    </ol>
    <P>{{counter}}</P>
    <button v-bind:disabled="flag" v-on:click="addClick"></button>
    <a v-bind:href="url"></a>
    <p>():{{now}}</p>
    <p>
      Ask a yes/no question:
      <input v-model="question" v-bind:placeholder="askQuestion">
    </p>
    <p>{{ answer }}</p>
    <my-title></my-title>
  </div>
</template>

<script>

  import Vue from 'vue'
  import jquery from 'jquery'

  //1,jquery.html('<div></div>')
  Vue.component('my-title', {
    template: `<button v-on:click="btnClick"></button>`,
    data() {
      return {
        //,
        msg: ''
      }
    },
    methods: {
      btnClick : function () {
        var json = {};
        jquery.ajax({
          url : 'http://192.168.1.160:8080/first/hello',
          type : 'get',
          data : json,
          dataType : 'jsonp',
          jsonp: "callbackParam", //callbackfunction
          timeout: 3000,
          contentType: "application/json; charset=utf-8",
          success : function(data) {
            alert(JSON.stringify(data));
          },
          error : function(err) {
            if(err.status == '404' && err.readyState == '4') {
              alert("!");
            }else if(err.status == '0' && err.readyState == '0' && err.statusText == 'timeout') {
              alert("!");
            }else{
              alert("ajax!" + JSON.stringify(err));
            }
          }
        })
      }
    }
  })


    export default {
      name: "study",
      data() {
        return {
          msg: 'hello world',
          tip: "",
          person: {
            username: "",
          },
          array: [1, 2, 3, 4, 5, 6],
          flag: true,
          url: "https://www.baidu.com",
          now: '123',
          types: [
            {
              text: "java"
            },
            {
              text: "c++"
            },
            {
              text: "c"
            }
          ],
          question: '',
          answer: 'I cannot give you an answer until you ask a question!',
          counter : 0
        }
      },
      methods: {//
        reverseMessage: function () {
          //html
          this.msg = this.msg.split('').reverse().join('')
          if (this.flag) {
            this.flag = false
            this.url = "https://www.acfun.com"
          } else {
            this.flag = true
            this.url = "https://www.baidu.com"
          }
          this.now = Date.now()
        },
        addClick : function (event) {
          this.counter = this.counter + 1;
          alert(JSON.stringify(event.target.tagName))
        },
        inputAlert : function () {
           alert("!")
        }
      },
      computed: {//
        nowTime: function () {
          this.now = Date.now()
        },
        askQuestion : function () {
          let ask = ""
          return ask
        }
      },
      watch: {
        //  `question` 
        question: function (newQuestion) {
          this.answer = 'Waiting for you to stop typing...'
          //this.getAnswer()
        }
      }
    }
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
</style>



